
$primary-color: #67AFCB;
$primary-color--darker: #347B98;

$secondary-color: #1A3E4C;
$secondary-color--darker: #092834;

$backgroud-color:#E4F1F6;

$border-width: 3px;
$border-radius: 8px;

$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

/* Reset */

* {
  margin: 0;
  padding: 0;

  font-family: $bodyFont;
}

h1 {
  font-size: 3rem;
  max-width: max-content;
  margin: 30px auto;

  color: $primary-color;
}

html{
  background-color: $backgroud-color;
}

main{
  padding: 0 5vw;
}

ul{
  list-style: none;
}

